import "./App.css";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Footer from "./components/Footer";
import Header from "./components/Header";
import LandingPage from "./screens/LandingPage/LandingPage";
import MyBooks from "./screens/MyBooks/MyBooks";
import SingleBook from "./screens/SingleBook/SingleBook";
import LoginScreen from "./screens/LoginScreen/LoginScreen";
import RegisterScreen from "./screens/RegisterScreen/RegisterScreen";
import CreateBook from "./screens/SingleBook/CreateBook";
import { useState } from "react";
import ProfileScreen from "./screens/ProfileScreen/ProfileScreen";
import RentBooks from "./screens/rentBooks/RentBook";

function App() {
  const [search, setSearch] = useState("");

  return (
    <Router>
      <Header setSearch={(s) => setSearch(s)} />
      <main className="App">
        <Route path="/" component={LandingPage} exact />
        <Route path="/login" component={LoginScreen} />
        <Route path="/register" component={RegisterScreen} />
        <Route
          path="/mybooks"
          component={({ history }) => (
            <MyBooks search={search} history={history} />
          )}
        />
        <Route path="/book/:id" component={SingleBook} />
        <Route path="/createbook" component={CreateBook} />
        <Route
          path="/rentbook"
          component={({ history }) => (
            <RentBooks search={search} history={history} />
          )}
        />
        <Route path="/profile" component={ProfileScreen} />
      </main>
      <Footer />
    </Router>
  );
}

export default App;
